.faceContainer {
  width: 231px;
  height: 231px;
  position: relative;
  margin: 24px auto;
  flex-shrink: 0;
}
.faceContainer .circle {
  border-radius: 231px;
  position: absolute;
}
.faceContainer .circle.circle1 {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.faceContainer .circle.circle2 {
  top: 15px;
  left: 15px;
  bottom: 15px;
  right: 15px;
}
.faceContainer .circle.circle3 {
  top: 30px;
  left: 30px;
  bottom: 30px;
  right: 30px;
}
.faceContainer.animated .circle.circle1 {
  animation: circle1 2s ease infinite;
}
.faceContainer.animated .circle.circle2 {
  animation: circle2 2s ease infinite;
}
.faceContainer.animated .circle.circle3 {
  animation: circle3 2s ease infinite;
}
.faceContainer.gray .circle.circle1 {
  background-color: #818e99;
}
.faceContainer.gray .circle.circle2 {
  background-color: #a0aab3;
}
.faceContainer.gray .circle.circle3 {
  background-color: #b8bfc6;
}
.faceContainer.blue .circle.circle1 {
  background-color: #005aff;
}
.faceContainer.blue .circle.circle2 {
  background-color: #4083ff;
}
.faceContainer.blue .circle.circle3 {
  background-color: #6ea0fd;
}
.faceContainer .face {
  background-size: 110px auto;
  background-position: center center;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.faceContainer .face.face1 {
  background-image: url('/static/assets/face/face1.png');
}
.faceContainer .face.face2 {
  background-image: url('/static/assets/face/face2.png');
}
.faceContainer .face.face3 {
  background-image: url('/static/assets/face/face3.png');
}
.faceContainer .face.face4 {
  background-image: url('/static/assets/face/face4.png');
}
@keyframes circle1 {
  25% {
    transform: scale(1.1) translateZ(0);
  }
  60% {
    transform: scale(1) translateZ(0);
  }
}
@keyframes circle2 {
  20% {
    transform: scale(1.25) translateZ(0);
  }
  45% {
    transform: scale(1) translateZ(0);
  }
}
@keyframes circle3 {
  15% {
    transform: scale(1.4) translateZ(0);
  }
  30% {
    transform: scale(1) translateZ(0);
  }
}
