.menu {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.menu__face {
  position: relative;
  width: 72px;
  height: 72px;
  margin: 16px;
  flex-shrink: 0;
}
.menu__face--free {
  background-color: #005aff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.5);
  border-radius: 72px;
  background-image: url('/static/assets/face/smallFace.png');
  background-position: center center;
  background-repeat: no-repeat;
}
.menu__face--premium {
  background-image: url('/static/assets/face/smallPremiumFace.png');
  background-color: #005aff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.5);
  border-radius: 72px;
  background-position: center 7px;
  background-repeat: no-repeat;
}
.menu__back {
  width: 15px;
  height: 15px;
  line-height: 13px;
  text-align: center;
  font-size: 22px;
  cursor: pointer;
  overflow: hidden;
  align-self: flex-start;
  padding: 16px;
}
.menu__premium_promo--premium {
  color: #818e99;
  font-size: 12px;
  text-align: center;
  margin-bottom: 15px;
  line-height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}
.menu__premium_promo--premium img {
  height: 12px;
  padding-right: 5px;
}
.menu__premium_promo--free {
  width: 220px;
  height: 36px;
  background-color: #26cb63;
  border-radius: 33.6px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  flex-shrink: 0;
}
.menu__premium_promo--free span {
  color: #fff;
}
.menu__premium_promo--free img {
  margin-right: 10px;
}
.menu__auth {
  width: 138px;
  height: 20px;
  border-radius: 33.6px;
  font-size: 9px;
  font-weight: bold;
  color: #fff;
  text-align: center;
  line-height: 20px;
  cursor: pointer;
  margin: 4px;
  flex-shrink: 0;
}
.menu__auth_sign-in {
  background-color: rgba(129, 142, 153, 0.5);
}
.menu__auth_check-mail {
  width: 220px;
  background-color: #ff5e5e;
}
.menu__auth_sign-out {
  width: 220px;
  background-color: #0059fd;
}
.menu__links {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.menu__link {
  border: 0.75px solid #e7e9eb;
  border-radius: 33.6px;
  background-color: #ffffff;
  height: 36px;
  width: 220px;
  text-align: center;
  line-height: 36px;
  color: #818e99;
  cursor: pointer;
  margin: 8px;
  text-decoration: none;
}
