.getting-started-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 278px;
  width: 100%;
  padding: 20px 24px;
  background-color: white;
  text-align: center;
}
.getting-started-info__title {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
  margin-top: 24px;
  margin-bottom: 4px;
}
.getting-started-info__title::first-letter {
  text-transform: uppercase;
}
.getting-started-info__description {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  margin: 4px 0 12px;
  color: #505962;
}
.getting-started-info__cta {
  margin: 12px 0;
}
.getting-started-info__policy {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  color: #8E8FA0;
