.input {
  position: relative;
  height: 42px;
  width: 248px;
  background: rgba(48, 57, 252, 0.05);
  left: -20px;
  margin-bottom: 10px;
}
.input input {
  position: absolute;
  left: 20px;
  z-index: 2;
  height: 42px;
  background: transparent;
  border: none;
  font-size: 16px;
  color: #000;
}
.input input:focus {
  outline: none;
}
.input input:-webkit-autofill {
  color: #242b33;
  transition: background-color 5000s ease-in-out 0s;
}
.input.notValid {
  box-shadow: 0 0 15px #ff5e5e;
}
.input.disabled input {
  color: #818e99;
}
.input .label {
  z-index: 1;
  color: #818e99;
  font-size: 16px;
  position: absolute;
  left: 20px;
  top: 13px;
}
.input .label.hidden {
  display: none;
}
