html,
body {
  margin: 0;
  padding: 0;
  width: 375px;
  height: 600px;
  font-size: 12px;
  font-family: 'Roboto', sans-serif;
  color: #818e99;
  overflow: hidden;
}
html *,
body * {
  box-sizing: border-box;
}
#page {
  height: 100%;
}
.root_container {
  height: 100%;
  display: grid;
  grid-template-rows: 1fr auto;
  grid-template-columns: 1fr;
}
