.selected_location {
  display: flex;
  margin-top: 12px;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.selected_location .globe {
  width: 21px;
  height: 21px;
  background-color: #818e99;
  border-radius: 21px;
  background-repeat: no-repeat;
  margin-right: 4px;
  cursor: pointer;
  background-size: auto 21px;
}
.selected_location .globe.optimal {
  background-image: url('/static/assets/elements/globe.png');
  background-position: center center;
  background-size: auto auto;
}
.selected_location span {
  cursor: pointer;
}
