.locations {
  display: flex;
  flex-direction: column;
  position: relative;
}
.locations__container {
  flex-shrink: 1;
  flex-grow: 0;
  overflow-y: auto;
  display: grid;
  grid-auto-flow: row;
  grid-gap: 32px;
  max-height: 482px;
  padding: 40px;
}
.locations__container--premium {
  max-height: 544px;
}
.locations__header {
  width: 100%;
  height: 56px;
  background: #FFFFFF;
  cursor: pointer;
  flex-shrink: 0;
}
.locations__header svg {
  margin: 16px;
}
.locations__title {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}
.locations__title::first-letter {
  text-transform: uppercase;
}
.locations__optional-group {
  display: grid;
  grid-auto-rows: auto;
  grid-row-gap: 12px;
}
.locations__list {
  display: flex;
  flex-direction: column;
}
.locations__item {
  margin: 8px 0px;
  border: 2px solid #E7E9EB;
  text-align: left;
}
.locations__item * {
  flex-grow: 0;
}
.locations__item span {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  flex-grow: 1;
  margin-left: 8px;
  justify-self: flex-start;
}
.locations__item span::first-letter {
  text-transform: uppercase;
}
.locations__go-premium {
  height: 62px;
  flex-shrink: 0;
  background-color: #005AFF;
  display: grid;
  grid-auto-rows: auto;
  grid-row-gap: 4px;
  padding: 12px 0;
  cursor: pointer;
  user-select: none;
}
.locations__go-premium h4 {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  color: white;
  text-align: center;
}
.locations__go-premium h4::first-letter {
  text-transform: uppercase;
}
.locations__go-premium p {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 14px;
  color: white;
  text-align: center;
}
