#screenUnavailable {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
#screenUnavailable .title {
  font-size: 18px;
  margin-left: 40px;
  margin-right: 40px;
  text-align: center;
}
#screenUnavailable .description {
  font-size: 16px;
  margin-left: 10px;
  margin-right: 10px;
  text-align: center;
  margin-top: 20px;
  color: #a0aac3;
}
#screenUnavailable .button {
  padding: 11.5px 5px;
  border-radius: 10px;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  min-width: 100px;
  align-items: center;
  background: #ffa400;
  border: 1.5px solid #ffa400;
  color: #fff;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 30px;
  cursor: pointer;
}
