.modal {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 8000;
}
.modal .bg {
  width: 375px;
  height: 600px;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1;
}
.modal .container {
  background: #fff;
  border-radius: 8.4px;
  width: 248px;
  z-index: 2;
  position: absolute;
  top: 75px;
  left: 64px;
}
.modal .container .circle {
  width: 68px;
  height: 68px;
  border-radius: 67px;
  position: absolute;
  top: -34px;
  left: 90px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-position: center center;
}
.modal .container .circle.blue {
  background-color: #005aff;
}
.modal .container .circle.green {
  background-color: #2be36f;
}
.modal .container .circle.red {
  background-color: #ff5e5e;
}
.modal .container .content {
  padding: 20px;
  margin-top: 20px;
}
.modal .container .content .title {
  color: #202020;
  margin-bottom: 10px;
  text-align: center;
  font-weight: bold;
}
.modal .container .content .message {
  text-align: center;
  margin-bottom: 10px;
  color: #ff5e5e;
}
.modal .container .content .description {
  color: #818e99;
  text-align: center;
}
.modal .container .content .description span {
  color: #000000;
  font-weight: bold;
}
.modal .container .content .buttons {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .container .content .buttons .button {
  height: 35px;
  border-radius: 35px;
  padding: 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  cursor: pointer;
}
.modal .container .content .buttons .button.blue {
  background: #005aff;
}
.modal .container .content .buttons .button.gray {
  background: #c6c6c6;
}
.modal .container .content .buttons .button.green {
  background: #2be36f;
}
.modal .container .content .buttons .button.red {
  background: #ff5e5e;
}
