.merchant-list {
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: repeat(auto-fit, 340px);
  grid-gap: 8px;
  padding: 16px;
  overflow: auto;
  width: 100%;
  height: 100%;
}
.merchant-list::-webkit-scrollbar {
  width: 4px;
}
.merchant-list::-webkit-scrollbar-thumb {
  background: #AFAFB4;
  border-radius: 12px;
}
.merchant-list::-webkit-scrollbar-thumb:hover {
  background: lightgray;
}
.merchant-list__title {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
}
