.merchant {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.merchant-header {
  position: relative;
  background: #FCE3A4;
}
.merchant-header__logo {
  grid-area: logo;
  width: 83px;
  height: 83px;
  background: white;
  box-shadow: 0 0 0px 56px #FFEDBF;
  border-radius: 50%;
  box-sizing: content-box;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  align-self: flex-end;
  justify-self: end;
}
.merchant-header__info {
  grid-area: info;
}
.merchant-header__cta {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  position: relative;
  background: #005AFF;
  border-radius: 100px;
  text-align: center;
  padding: 10px;
  margin: 16px;
  color: white;
  cursor: pointer;
  z-index: 2;
}
.merchant-header__content {
  position: relative;
  padding: 16px;
  display: grid;
  height: 154px;
  grid-template-areas: 'info logo';
  z-index: 1;
  overflow: hidden;
}
.merchant-header__content--with-caa {
  height: 215px;
  grid-template-areas: 'info logo' 'button button';
}
.merchant-header__caa-button {
  grid-area: button;
  justify-items: center;
  align-items: flex-end;
  display: flex;
}
.merchant-header__title {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  margin-top: 22px;
  margin-right: 64px;
}
.merchant-header__title::first-letter {
  text-transform: uppercase;
}
.merchant-header__message {
  position: relative;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  width: 100%;
  padding: 16px 24px;
  display: flex;
  align-items: center;
  color: #2A2A4B;
  background: #FCE3A4;
  border-radius: 4px;
  z-index: 2;
}
.merchant-header__message::first-letter {
  text-transform: uppercase;
}
.merchant-header__cashback {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 44px;
  line-height: 52px;
  margin-top: 8px;
}
.merchant-header__cashback::first-letter {
  text-transform: uppercase;
}
