.merchant-header__close {
  position: absolute;
  cursor: pointer;
  top: calc(50% - 5px);
  right: 10px;
  width: 20px;
  height: 10px;
  background-image: url("data:image/svg+xml,%3Csvg%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%3Cpath%20d%3D%22M1%201L11%2011%22%20stroke%3D%22%23FAFBFC%22%20stroke-width%3D%222%22%20stroke-linecap%3D%22round%22%2F%3E%0A%3Cpath%20d%3D%22M11%201L1%2011%22%20stroke%3D%22%23FAFBFC%22%20stroke-width%3D%222%22%20stroke-linecap%3D%22round%22%2F%3E%0A%3C%2Fsvg%3E%0A");
  background-repeat: no-repeat;
  background-size: contain;
}
.merchant-header__sparkle {
  position: absolute;
  height: 8px;
  width: 8px;
  background-repeat: no-repeat;
