.merchant-header__close-guide {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  padding: 6px;
  border-radius: 10px;
  color: white;
  justify-self: end;
  align-self: flex-end;
  cursor: pointer;
}
.merchant-header__close-guide:hover {
  background-color: white;
  color: #005AFF;
}
