@keyframes popup {
  from {
    transform: translateY(300px);
  }
  to {
    transform: translateY(0);
  }
}
.limited_time_offer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  background: #2A2A4B;
  height: 100px;
  width: 92%;
  margin: 14px 16px 0;
  padding: 20px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  animation: 0.5s ease-in-out popup;
}
.limited_time_offer__star {
  position: absolute;
}
.limited_time_offer__star--top_left {
  width: 10px;
  height: 10px;
  top: 10px;
  left: 10px;
}
.limited_time_offer__star--top_right {
  width: 10px;
  height: 10px;
  top: 10px;
  right: 10px;
}
.limited_time_offer__star--bottom_left {
  width: 14px;
  height: 14px;
  bottom: 8px;
  left: 8px;
}
.limited_time_offer__star--bottom_right {
  width: 14px;
  height: 14px;
  bottom: 8px;
  right: 8px;
}
.limited_time_offer__top {
  position: absolute;
  top: -14px;
  width: 160px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  height: 28px;
  display: flex;
  justify-content: space-evenly;
  justify-items: center;
  align-items: center;
  padding: 6px;
  background: #FFD05A;
  border-radius: 100px;
}
.limited_time_offer__discount {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  font-weight: 900;
}
.limited_time_offer__discount::first-letter {
  text-transform: uppercase;
}
.limited_time_offer__text {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}
.limited_time_offer__text::first-letter {
  text-transform: uppercase;
}
.limited_time_offer__delimiter {
  color: #2A2A4B;
}
.limited_time_offer__time {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
}
.limited_time_offer__time::first-letter {
  text-transform: uppercase;
}
.limited_time_offer__button {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  background: #FFFFFF;
  border-radius: 100px;
  width: 115px;
  padding: 8px 16px;
  justify-items: center;
  align-items: center;
  flex-shrink: 0;
  text-decoration: none;
}
.limited_time_offer__button::first-letter {
  text-transform: uppercase;
}
