.merchant-card {
  display: grid;
  grid-auto-flow: row;
  grid-template-areas: 'discount logo' 'description logo';
  grid-template-columns: auto 30%;
  grid-gap: 4px;
  padding: 16px;
  height: 114px;
  background: #FFFFFF;
  border: 1px solid #E7E9EB;
  box-sizing: border-box;
  border-radius: 4px;
}
.merchant-card__description {
  grid-area: description;
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 40px;
}
.merchant-card__discount {
  grid-area: discount;
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
  font-weight: bold;
}
.merchant-card__discount::first-letter {
  text-transform: uppercase;
}
.merchant-card__logo {
  grid-area: logo;
  width: 80px;
  height: 80px;
  padding-left: 20px;
  box-sizing: content-box;
  border-left: lightgrey solid 1px;
  background: white;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
