#screenMain {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-items: center;
  align-items: center;
}
#screenMain .main__button-group {
  display: grid;
  justify-content: center;
  grid-auto-rows: auto;
  grid-gap: 16px;
  margin: 16px 64px;
  padding: 0;
  width: 100%;
}
#screenMain .main__footer {
  display: flex;
  flex-direction: column;
  position: fixed;
  bottom: 52px;
  left: 0;
  right: 0;
}
