.footer {
  width: 100%;
  box-shadow: 0px -1px 0px #E7E9EB;
  height: 52px;
  z-index: 5000;
  background: #FFFFFF;
}
.footer__row {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  justify-items: center;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0;
}
.footer__item {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 0;
  margin: 0;
  cursor: pointer;
  border-bottom: 4px solid transparent;
}
.footer__item--active {
  border-bottom: 4px solid #005AFF;
}
.footer__icon {
  display: block;
  margin: auto;
}
